@import url(https://fonts.googleapis.com/css?family=Montserrat:500&display=swap);
* {
  margin: 0;
  padding: 0;
}

#root {
  height: 100%;
}

li, a {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: #edf0f1;
  text-decoration: none;
}

.toggle-btn {
  padding: 8px 25px;
  margin: 10px;
  background-color: rgb(25, 64, 131);
  border: none;
  border-radius: 50px;
  color: white;
}

body, html {
  height: 100%;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 70px;
  text-transform: uppercase;
  margin: 0;
}

.banner-img {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),url(/virtual-canvas/static/media/dance-floor.5deb96c1.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 70vw;
}

.banner-text {
  text-align: center;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  width: 55%;
  padding: 40px 0;
  border-top-left-radius: 70px;
  border-bottom-right-radius: 70px;
  background-color: rgba(0,0,0,0.3);
  color: #fff;
  border: 10px inset #fff;
}

.about-container {
    background: rgb(40, 88, 129);
    text-align: center;
    padding: 1em 2em;
    height: 600px;
    display: flex;
    justify-content: space-evenly;
    color: white;
}

.about-card {
  height: 100%;
  width: 855px;
  display: grid;
  grid-template-columns: 1fr 1.25fr;
}

.left {
  grid-column: 1 / 2;
  height: 100%;
  object-fit: cover;
  margin: 0;
}

.right {
  grid-column: 2 / 3;
  padding: 4em 0 0;
  background: rgba(6, 20, 30);
  max-width: 600px;
  display: flex;
  flex-direction: column;
}

.text {
    flex: 7 1;
    margin: 2em;
}

.text h1 {
    margin: 1em 0;
}

h3 {
  font-size: 2em;
}

.text p {
    line-height: 1.7rem;
}

.links {
    flex: auto;
    display: flex;
}

.links button {
    flex: 1 1;
    color: white;
    background-color: #ff0546;
    border: none ;
}

.user-show {
  padding: 20px;
  background: rgb(19, 20, 23);
  color: white;
  height: 100%;
}

.canvas-show {
  height: 100%;
  width: 100%;
  padding: 30px;
}

.canvas-header {
  text-align: center;
}

.canvas-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-height: 625px;
} 

.canvas {
  width: 600px;
  margin-top: 10px;
  text-align: center;
  box-shadow: 0px 50px 100px 0px rgba(9,24,50, 0.25);
}

.tools-container {
  margin-left: 20px;
  margin-top: 10px;
  height: 625px;
  overflow-y: scroll;
  box-shadow: 0px 50px 100px 0px rgba(9,24,50, 0.25);
}

.tool {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding: 10px;
}

.tool-burst {
  display: grid;
  grid-template-rows: 1fr 1fr;
  padding: 10px;
}

.canvas-settings {
  padding: 15px;
}

.burst1, .burst2 {
  height: 150px;
  padding: 10px;
}

.burst2 {
  display: flex;
}

.toolbox1, .toolbox2, .toolbox3, .toolbox4 {
  height: 150px;
  padding: 10px;
}

.toolbox4 {
  display: flex;
}
.canvas-card{padding:1em;background-color:#1f2229;color:white;border-radius:10px;box-shadow:10px 10px 5px 0px rgba(0,0,0,0.75);transition:all 0.3s ease 0s}.canvas-card h4{margin:1em 0}.canvas-card p{font-size:.75em;margin:1em 0}.canvas-card:hover{box-shadow:0 40px 40px rgba(0,0,0,0.25)}

.canvases-index{display:flex;flex-flow:column;height:100%;background:#131417;padding:4em 2em;color:white}.canvases-index #divider{background-color:#02bdfc;width:100%;height:.25em}.canvas-form{justify-content:center}.canvas-list{overflow:auto}.canvas-list ul{display:flex;list-style:none;display:flex;flex-wrap:wrap-reverse;justify-content:space-around}.canvas-list ul li{margin:1em}

.canvas-table{width:100%;overflow-x:auto;padding:1em 0}.canvas-table ul{display:flex;list-style:none}.canvas-table ul li{margin-right:1em}.canvas-table ul li button{color:white;background-color:#641400;padding:.75em 1em;border-radius:.5em;border:none;cursor:pointer}

header{display:flex;justify-content:flex-end;align-items:center;padding:30px 10%;background-color:#091833}.nav-links{list-style:none}.nav-links button{padding:8px 25px;background-color:#ea00d9;border:none;border-radius:50px;cursor:pointer}.nav-links li{display:inline-block;margin-top:12px;padding:0px 20px}.nav-links li a{transition:all 0.3s ease 0s}.nav-links li a:hover{color:#133e7c}

.App{text-align:center}

